import React, { Fragment, useEffect, useState } from "react";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import Link from "@mui/material/Link";
import "./App.css";
import {
  AppBar,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Toolbar,
} from "@mui/material";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import TravelExploreIcon from "@mui/icons-material/TravelExplore";
interface Section {
  "title": string;
  "url": string;
}
interface Details {
  "name": string;
  "sections": Section[];
}

function App() {
  const [detailsList, setDetailsList] = useState<Details[]>();

  const fetchDetails = async () => {
    const response = await fetch(
      "https://raw.githubusercontent.com/saravanakumargn/tn-esevai-web/main/details.json"
    );
    const data = await response.json();
    setDetailsList(data);
  };
  useEffect(() => {
    fetchDetails();
  }, []);

  return (
    <Container maxWidth="md" disableGutters>
      <AppBar>
        <Toolbar>
          <Typography variant="h6" display="inline" noWrap color={"#fff"}>
             - (unofficial)
          </Typography>
        </Toolbar>
      </AppBar>
      <Toolbar />
      <Container sx={{ my: 4 }}>
        {/* <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="center"
        >
          <Typography variant="h6" component="div" color="secondary">
             - (unofficial)
          </Typography>
        </Grid> */}

        {detailsList?.map((item) => (
          <Accordion key={item.name} defaultExpanded disableGutters>
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>{item.name}</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <List>
                {item?.sections?.map((section) => (
                  <Fragment key={`${section.title}_${section.url}`}>
                    <ListItem
                      key={`${section.title}_${section.url}`}
                      component={Link}
                      color="inherit"
                      href={section.url}
                      disablePadding
                    >
                      <ListItemButton>
                        <ListItemIcon>
                          <TravelExploreIcon />
                        </ListItemIcon>
                        <ListItemText primary={section.title} color="#000" />
                      </ListItemButton>
                    </ListItem>
                  </Fragment>
                ))}
              </List>
            </AccordionDetails>
          </Accordion>
        ))}
      </Container>
    </Container>
  );
}

export default App;
